  vertical-align: top;
  width: 40px;
  height: 24px;
  background-color: transparent;
  cursor: pointer;
}

.switch.switch-default .switch-input {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
}

.switch.switch-default .switch-label {
  position: relative;
  display: block;
  height: inherit;
  font-size: 10px;
  font-weight: 600;
  text-transform: uppercase;
  background-color: #fff;
  border: 1px solid #e9ecef;
